$(function($){


	mud.behaviour.init();

	var picker = $('#color-picker');
	$(picker).hide();
	$('.form').on('click','input[name^="colour"]',function() {		
		$(picker).show();
		$(this).after( picker ).show();
	}).on('blur','input[name^="colour"]',function() {
		$(picker).hide();
	});
	
	$('.simple_color').simpleColor({
		displayColorCode: true,
		onSelect: function(hex, element) {
			var wibble = tinycolor.mostReadable(hex, ['#fff', '#000']);
			// alert(wibble);
			alert("color picked! " + hex + " for input #" + element.attr('class'));
			$(this).css('color',wibble);
		}
	});

	// var wibble = tinycolor('#006699');
	var wibble = tinycolor.mostReadable('#006699', ['#fff', '#000']);
	// alert(wibble);
});
